body {
  --dkgreen: #61892F;
  --ltgreen: #86C232;
  --dkgrey: #222629;
  --mdgrey: #474B4F;
  --ltgrey: #6B6E70;
  background-color: var(--dkgrey);
  color: var(--ltgreen);
  font-family: monospace;
  font-size: 14px;
  margin: 0px; }

* {
  box-sizing: border-box; }

article {
  background-color: var(--dkgrey);
  background-image: repeating-linear-gradient(rgba(107, 110, 112, 0.1), rgba(107, 110, 112, 0.1) 1px, transparent 1px, transparent 4px); }

h2 {
  position: absolute;
  z-index: -20; }

#navbar {
  position: fixed;
  top: 0;
  z-index: 1;
  padding: 0 20px 7px 20px;
  width: 100vw;
  background-color: rgba(35, 39, 42, 0.8); }
  #navbar ul {
    list-style: none;
    display: flex;
    padding: 0px;
    margin: 0px; }
  #navbar li {
    display: inline-block;
    margin: 0px;
    padding: 0px; }
  #navbar a {
    display: inline-block;
    text-decoration: none;
    background: var(--dkgrey);
    color: var(--ltgreen);
    padding: 10px 20px;
    margin-right: 7px;
    border-right: 2px solid var(--ltgreen);
    border-left: 2px solid var(--ltgreen);
    border-bottom: 2px solid var(--ltgreen); }
  #navbar a:hover, #navbar a:focus {
    background-color: var(--dkgreen);
    color: white;
    box-shadow: inset 1px 1px 5px var(--dkgrey); }

#a-welcome {
  color: white;
  font-weight: bold; }

#welcome-section {
  box-sizing: border-box;
  height: 100vh;
  width: 75%;
  margin: 0px auto; }

#welcome-grid {
  height: 100%;
  margin: 0px;
  padding: 0px;
  display: grid;
  grid-template: "greet greet name ." ". tagline tagline tagline";
  grid-gap: 5px; }
  #welcome-grid span {
    display: inline-block;
    text-align: center; }

#name {
  font-size: 45px;
  margin: 0px;
  padding: 10px 0px;
  text-align: center;
  grid-area: name;
  align-self: end;
  background: var(--ltgrey);
  color: var(--dkgrey); }

#greeting {
  font-size: 20vh;
  grid-area: greet;
  align-self: end;
  background: var(--ltgreen);
  color: var(--dkgrey); }

#tagline {
  padding: 10px 0px;
  font-weight: bold;
  font-size: 20px;
  grid-area: tagline;
  align-self: start;
  background: var(--dkgreen);
  color: var(--dkgrey); }

#projects {
  height: 100vh;
  background-color: var(--dkgreen);
  background-image: repeating-linear-gradient(rgba(134, 195, 50, 0.1), rgba(134, 195, 50, 0.1) 1px, transparent 1px, transparent 4px);
  padding: 50px 10px 10px 10px; }

.proj-flex {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center; }
  .proj-flex a {
    text-decoration: none;
    border-radius: 50%; }

.project-tile {
  background-position: center;
  background-size: auto 300px;
  background-repeat: no-repeat;
  background-color: rgba(107, 110, 112, 0.6);
  width: 300px;
  height: 300px;
  margin: 8px;
  border: 10px solid var(--dkgrey);
  border-radius: 50%;
  box-shadow: 1px 1px 3px var(--mdgrey);
  z-index: 0;
  text-align: center;
  vertical-align: center; }
  .project-tile span {
    display: inline-block;
    width: 280px;
    margin: 80% auto auto auto;
    padding: 10px;
    background-color: var(--dkgrey);
    color: var(--ltgreen);
    font-weight: bold; }

.project-tile:hover {
  border-color: var(--mdgrey); }

footer {
  padding: 15px 10px;
  height: auto; }
  footer a {
    display: inline-block;
    margin: 5px 10px;
    text-decoration: none;
    color: var(--ltgreen); }
  footer a::before {
    content: "{ "; }
  footer a::after {
    content: " }"; }

@media only screen and (max-width: 650px) {
  #welcome-grid {
    grid-template: "." "greet" "." "name" "tagline";
    grid-template-rows: .25fr auto .25fr auto auto;
    align-content: center; } }

@media only screen and (max-width: 500px) {
  #navbar {
    border-bottom: 1px solid var(--dkgreen);
    width: 100vh;
    height: 40px;
    right: 100vw;
    transform: rotate(-90deg);
    transform-origin: 100% 0; }
    #navbar ul {
      width: 100%;
      flex-flow: row-reverse nowrap;
      justify-items: flex-end; }
    #navbar a {
      position: relative;
      top: 7px; }
    #navbar a:hover, #navbar a:focus {
      top: 13px; }
    #navbar #projects:target #a-proj {
      top: 13px; }
  #projects, #skills {
    padding-left: 30px; }
  footer {
    padding-left: 30px; } }

@media only screen and (max-width: 380px) {
  #name {
    font-size: 30px; }
  #tagline {
    font-size: 10px; } }
